.form {
  color: $color-default-white;
  background-image: linear-gradient(139deg, rgb(15, 88, 144) 34%, $deep-sky-blue 34%);

  @media (max-width: $tablet-width) {
    background-color: $deep-sky-blue;
    background-image: none;
  }
}

.form__container {
  padding: 170px 30px 250px;
  max-width: 1220px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 115px;

  form {
    max-width: 580px;

    @media (max-width: $tablet-width) {
      max-width: 670px;
      margin: 0 auto;
    }
  }

  @media (max-width: $tablet-width) {
    display: block;
    padding: 50px 50px 125px;
  }

  @media (max-width: $mobile-width) {
    padding: 50px 25px 90px;
  }
}

.form__heading-wrapper {
  h2 {
    margin: 0;
    padding: 0;
    font-family: $roboto-condensed;
    font-weight: 400;
    font-size: 36px;
    line-height: 51px;
    margin-bottom: 15px;

    @media (max-width: $tablet-width) {
      margin-bottom: 10px;
    }

    @media (max-width: $mobile-width) {
      font-size: 30px;
      line-height: 42px;
      margin-bottom: 20px;
    }
  }

  p {
    margin: 0;
    padding: 0;
    font-family: $pt-mono;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 35px;

    @media (max-width: $tablet-width) {
      margin-bottom: 25px;
    }

    @media (max-width: $mobile-width) {
      font-size: 14px;
      line-height: 19px;
      margin-bottom: 40px;
    }
  }
}

.form__input-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;

  @media (max-width: $mobile-width) {
    display: flex;
    flex-direction: column;
  }
}

.form__input {
  input {
    width: 100%;
    font-family: $pt-mono;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: $color-default-white;
    padding: 15px;
    box-sizing: border-box;
    background: rgba(4, 20, 39, 0.15);
    border: 1px solid transparent;
    border-radius: 3px;

    &::placeholder {
      font-family: $pt-mono;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      color: $color-default-white;
    }

    &:hover,
    &:focus {
      outline: none;
      border: 1px solid $black-pearl;
    }

    &:active,
    &:focus-within {
      outline: none;
      background-color: rgba($black-pearl, 0.5);
    }
  }

  textarea {
    width: 100%;
    font-family: $pt-mono;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: $color-default-white;
    padding: 15px;
    box-sizing: border-box;
    background: rgba(4, 20, 39, 0.15);
    border: 1px solid transparent;
    border-radius: 3px;

    &::placeholder {
      font-family: $pt-mono;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      color: $color-default-white;
    }

    &:hover,
    &:focus {
      outline: none;
      border: 1px solid $black-pearl;
    }

    &:active,
    &:focus-within {
      outline: none;
      background-color: rgba($black-pearl, 0.5);
    }
  }

  &--question {
    grid-column: 1 / 3;
    margin-bottom: 35px;
  }

  &--agreement {
    grid-column: 2 / 3;
    font-family: $pt-mono;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    opacity: 0.8;

    label {
      display: flex;
      align-items: center;
      padding-top: 13px;
    }

    input {
      appearance: none;
      display: block;
      position: relative;
      width: 20px;
      height: 15px;
      margin-right: 10px;
      border: 1px solid $color-default-white;
      border-radius: 1px;
      cursor: pointer;
      padding: 0;
      background-color: transparent;
      margin-left: 5px;

      &:checked {
        &::after {
          content: "";
          position: absolute;
          top: 13%;
          left: 13%;
          display: block;
          width: 7px;
          height: 5px;
          border: 1px solid $color-default-white;
          border-right: none;
          border-top: none;
          transform: rotate(-45deg);
        }
      }
    }

    @media (max-width: $mobile-width) {
      order: 4;
    }
  }


  &--username,
  &--userphone {
    margin-bottom: 20px;
  }

  &--button {
    background: $black-pearl;
    width: 265px;
    font-family: $roboto-condensed;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: $color-default-white;
    border-radius: 3px;
    padding: 15px 25px;
    cursor: pointer;
    display: inline-block;
    grid-column: 1 / 2;
    grid-row: 3/4;
    border: 1px solid transparent;

    &:hover,
    &:focus {
      outline: none;
      border: 1px solid $black-pearl;
      background-color: rgba($black-pearl, 0.8);
    }

    &:active,
    &:focus-within {
      outline: none;
      background-color: rgba($black-pearl, 0.5);
    }

    @media (max-width: $mobile-width) {
      width: 100%;
    }
  }
}

.form__image {
  max-width: 510px;
  padding-top: 80px;

  @media (max-width: $tablet-width) {
    display: none;
  }
}
